body {
  font-family: 'Lato', sans-serif; 
  font-size: 17px;
  line-height: 1.5;
  color: $c-darkblue;
}



// ----- TYPO -----
p{
    margin: 25px 0;
}
.title--big {
    height: 60px;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
    margin: 0;
    width: 200px;
    position: absolute; top: 15px; left: 15px;
    background: {
        image: url(../../assets/images/logo.svg);
        repeat: no-repeat;
        position: left top;
        size: contain;
    }
}
.title--large{
    font-family: 'Abril Fatface', cursive;
    font-size: 34px;
    font-weight: normal;
    line-height: 1.3;
    margin: 0;
    padding-top: 25px;
    max-width: 700px;
}
.title--medium{
    font-size: 24px;
    line-height: 1.4;
    font-weight: 900;
    margin: 0;
    padding-top: 25px;
}
.title--light{
    color: #fff;
}
//----HEADER
.header{
    width: 100%;
    height: 75px;
    
    .container__plante{
        height: 175px;
        width: 175px;
        position: absolute; top: 0; right: 0;
        background:{
            image: url(../../assets/images/plantes1.svg);
            position: right top;
            repeat: no-repeat;
            size: contain;
        }
    }
}
//----SECTIONS
.section{
    padding: 50px 25px;
    box-sizing: border-box;
}
.section--bienvenue{
    padding-top: 50px;
    width: 100%;
    position: absolute; top: 50%; 
    transform: translateY(-50%);
    .bienvenue--txt, .poisson-big{
        display: inline-block;
    }
    .bienvenue__txt{
        .title--large{
            padding-top: 0;
        }
    }
    .poisson-big{
        display: none;
        max-height: 300px;
    }
}
      .container__vague{
        display: block;
        width: 100%;
        height: 200px;
        position: absolute;
        bottom: 55px;
        background:{
            image: url(../../assets/images/vague-big.svg);
            repeat: repeat-x;
            position: left bottom;
            size: 300%;
            }
        animation: wave 40s infinite linear;
        }
        @keyframes wave{
            from{
                background-position: left bottom;
            }
            to{
                background-position: right bottom;
            }
        }

.section--dechets{
        position: relative;
.dechets__liste{
            display: block;
            list-style: none;
            margin-top: 50px;
            padding: 0;
            .dechets__el{
                display: inline-block;
                margin: 25px 0;
                box-sizing: border-box;
                text-align: center;
                width: 49%;
                & p{
                    margin: 45px 0 0 0;
                }
                .percent{
                    color: $c-red;
                    font-family: 'Abril Fatface', cursive;
                    font-size: 24px;
                    line-height: 1;
                    padding: 35px 30px 30px 30px;
                    vertical-align: middle;
                    background: {
                        repeat: no-repeat;
                        position: center;
                        size: contain;
                    }
                }
                .percent--29{
                        background-image: url(../../assets/images/percent29.svg);
                }
                .percent--35{
                        background-image: url(../../assets/images/percent35.svg);
                }
                .percent--21{
                        background-image: url(../../assets/images/percent21.svg);
                }
                .percent--15{
                        background-image: url(../../assets/images/percent15.svg);
                }
            }
        }
    .container__vague--bluetop{
            height: 200px;
            width: 100%;
            position: absolute; bottom: 0; left: 0;
            background-image: url(../../assets/images/vague.svg);
            background-repeat: no-repeat;
            background-size: 101%;
            background-position: bottom;
        }
}
//------SECTION BLUE
.section--blue{
    background-color: $c-darkblue;
    color: #fff;
    margin-top: -2px;
    .left, .right{
        position: relative;
        padding-bottom: 125px;
    }
    .poisson, .sac, .bouteille{
        position: absolute;
    }
    .poisson{
        right: 10px;
        height: 80px;
        width: 150px;
        animation: upAndDown 5s infinite linear;
        background: {
            image: url(../../assets/images/poisson3.svg); 
            repeat: no-repeat;
        }
        @keyframes upAndDown{
            0%{
               transform: translateY(0px);
            }
            25%{
                transform: translateY(5px);
            }
            75%{
                transform: translateY(-5px);
            }
            100%{
                transform: translateY(0px);
            }
        }
        
    }
    .sac{
        height: 125px;
        width: 150px;
        right: 20px;
        background: {
            image: url(../../assets/images/sac.svg); 
            repeat: no-repeat;
        }
        animation: sacFloat 15s infinite linear;
        @keyframes sacFloat{
            0%{
               transform: translate(0px, -30px) rotate(0deg); 
            }
            25%{
                transform: translate(15px, -15px) rotate(-10deg);
            }
            50%{
                transform: translate(0) rotate(-5deg);
            }
            75%{
                transform: translate(-15px, -15px) rotate(-10deg);
            }
            100%{
                transform: translate(0px, -30px) rotate(0deg);
            }
        }
    }
    .bouteille{
        left: 30px;
        bottom: 10px;
        height: 90px;
        width: 50px;
        transform: rotate(-30deg);
        background: {
            image: url(../../assets/images/bouteille.svg); 
            repeat: no-repeat;
        }
        animation: boutFloat 10s infinite linear;
        @keyframes boutFloat{
            0%{
               transform: translate(0) rotate(140deg);
            }
            25%{
                transform: translate(-10px, 15px) rotate(150deg);
            }
            75%{
                transform: translate(4px, 10px) rotate(130deg);
            }
            100%{
                transform: translate(0) rotate(140deg);
            }
        }
    }
}
.section--mode{
    position: relative;
    margin-top: -1px;
    padding-bottom: 75px;
    .container__vague--bluebottom{
        height: 200px;
        width: 100%;
        position: absolute; top: 0; left: 0;
        background-image: url(../../assets/images/vague2.svg);
        background-repeat: no-repeat;
        background-size: 102%;
        background-position: top;
    }
    .container__vague--redtop{
        height: 200px;
        width: 100%;
        position: absolute; bottom: 0; left: 0;
        background-image: url(../../assets/images/vague-red2.svg);
        background-repeat: no-repeat;
        background-size: 102%;
        background-position: bottom;
    }
    .poubelles{
        margin-top: 25px;
    }
}
.section--red{
    background-color: $c-red;
    margin-top: -1px;
    color: #fff;
    .title--large{
        display: block;
        margin: 0 auto;
    }
    .regles__el{
        list-style: none;
        position: relative;
        z-index: 1;
    }
    .regles__el:before{
        content: attr(data-lettrine);
        color: $c-corail;
        font-size: 150px;
        line-height: 1;
        font-family: 'Abril Fatface', cursive;
        display: block;
        position: absolute;
        z-index: -1;
        top: -5px;
        left: 0;
    }
    ul{
        list-style-position: outside;
        padding-left: 0;
    }
    //  -------  SLIDER
    .slider{
        margin: 0 auto;
        max-width: 800px;
    }
    .slide-viewer{
        position: relative;
        overflow-x: hidden;
        height: 70vh;
        width: 100%;
        border: 2px solid $c-corail;
        border-radius: 5px;
    }
    .slide-group{
        width: 100%;
        height: 100%;
        position: relative;
    }
    .slide{
        width: 100%;
        height: 100%;
        display: none;
        position: absolute;
        padding: 0 5px 0 25px;
        box-sizing: border-box;
    }
    .slide:first-child{
        display: block;
    }
    .slide-buttons{
        text-align: center;
        margin-top: 15px;
    }
    .slide-btn{
        border: none;
        background: none;
        color: $c-corail;
        font-size: 200%;
        line-height: 0.5em;
        outline: 0;
        &:hover{
            cursor: pointer;
            transform: scale(1.1);
        }
    }
    .slide-btn.active{
        color: #fff;
    }
}
// ------- SECTION BOTTOM
.section--bottom{
    padding-bottom: 200px;
    margin-bottom: 40px;
    margin-top: -1px;
    background-image: url(../../assets/images/plantes-footer.svg);
    background-repeat: no-repeat;
    background-position: 35% bottom;
    background-size: 350%;
    position: relative;
    .container__vague--redbottom{
        height: 200px;
        width: 100%;
        position: absolute; top: 0; left: 0;
        background-image: url(../../assets/images/vague-red.svg);
        background-repeat: no-repeat;
        background-size: 102%;
        background-position: top;
    }
    .avantages__liste{
        margin: 0;   
    }
}

.page { 
    display: none; 
}
.page.active { 
     display: block; 
}
 
//---------SECTION ASTUCES
.section--astuces, .section--recettes{
        padding: 50px 0 75px 0;
        .title--large, .title--medium{
            padding-left: 25px;
        }
        .astuces__liste, .recettes__liste{
            overflow-x: auto;
            overflow-y: hidden;
            white-space: nowrap;
            padding: 0 10px;
        }
        .astuces__el, .recettes__el{
            display: inline-block;
            box-shadow: 0 2px 10px rgba(0,0,0,.2);
            width: 225px;
            height: 250px;
            list-style: none;
            background-color: #fff;
            border-radius: 5px;
            vertical-align: middle;
            margin: 15px;
            position: relative;
            & a{
                text-decoration: none;
                color: inherit;
            }
        }
        .astuces__el--last, .recettes__el--last{
            padding: 25px;
            box-sizing: border-box;
            position: relative;
            &:after{
                position: absolute; top: 0; right: -45px;
                content: "";
                height: 100%;
                width: 50%;
                background-image: url(../../assets/images/poisson1b.svg);
                background-repeat: no-repeat;
                background-size: 90%;
                background-position: center;
            }
            p{
                margin: 0;
                width: 60%;
                height: auto;
                position: absolute;
                top: 50%; 
                transform: translateY(-50%);
                white-space: normal;
            }
        }
        .astuces__el--soon{
            position: relative;
            .strong{
                font-weight: bold;
                display: block;
                margin: 0 auto;
                position: absolute;
                width: 100%;
                top: 50%; 
                transform: translateY(-50%);
                white-space: normal;
            }
        }
    .yaourtBook{
        margin: 25px auto 0 auto;
        display: block;
        width: 100%;
        max-width: 400px;
    }
    .video{
        display: block;
        max-width: 100%;
    }
    .popup-close{
        position: fixed;
        border: none;
        width: 18px;
        height: 18px;
        top: 40px; right: 15px;
        background-image: url(../../assets/images/cross.svg);
        background-repeat: no-repeat;
        background-color: transparent;
        color: transparent;
        outline: 0;
        display: block;
        overflow: hidden;
        text-indent: 100%;
        white-space: nowrap;
        &:hover, &:focus{
            cursor: pointer;
            transform: scale(1.1);
        }
    }
    .popup-astuce__el, .popup-recette__el{
        display: none;
    }
    .popup-astuce__el.active, .popup-recette__el.active{
        display: block;
        padding: 25px 50px;
        box-sizing: border-box;
        position: fixed;
        width: calc(100%);
        height: calc(100% - 50px);
        top: 25px;
        left: 0;
        background-color: #fff;
        z-index: 999;
        border-radius: 10px;
        box-shadow: 0 0 200px rgba(0,0,0,.7);
        overflow: auto;
        .title--large{
            text-align: center;
            padding-left: 0;
        }
        .title--medium{
            margin: 50px 0 25px 0;
            padding: 0;
        }
        li{
            padding-bottom: 17px;
        }
        ul{
            padding: 0;
            list-style-position: outside;
            list-style: disc;
        }
        & .title--large, & .title--medium, & p, & ul{
                max-width: 600px;
                margin-left: auto; margin-right: auto;
        }
        .popup__link{
            color: $c-purple;
            text-decoration: none;
            &:hover, &:focus{
                color: $c-red;
                transition: .3s;
            }
        }
        .small{
            text-align: center;
            padding-top: 25px;
        }
        .liste--number{
            list-style: decimal;
        }
        
    }
    .bg-r, .bg-a{
        height: 75%;
        width: 100%;
        border-radius: 5px 5px 0 0;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center 50%;
    }
    .bg-a01{
        background-image: url(../../assets/images/vrac.jpg);
    }
    @media (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi) {
         .bg-a01{
