    }
    .bg-a03{
        background-image: url(../../assets/images/beewrap.jpg);
    }
     @media (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi) {
        .bg-a03{
        background-image: url(../../assets/images/beewrap.jpg);
        }
    }
    .bg-a04{
        background-image: url(../../assets/images/legumes.jpg);
        background-position: center 20%;
    }
    .bg-a05{
        background-image: url(../../assets/images/ice-cubes.jpg);
    }
   
    .bg-r01{
        background-image: url(../../assets/images/yaourtiere.jpg);
