            grid-row: 2 / 5;
        }
    }
     .section--bottom{
         padding-bottom: 300px;
         box-sizing: border-box;
         margin-bottom: 0;
         background-position: center 101%;
         background-size: 135%;
         display: block;
        .title--large{
            margin: 0 auto;
            text-align: center;
        }
        .avantages__liste{
            margin: 25px 0;
        }
         .avantages__el{
             display: inline-block;
             vertical-align: top;
             width: 33%;
             padding: 0 50px;
             box-sizing: border-box;
         }
         .container__figure{
             display: grid;
             grid-template-columns: repeat(8, 1fr);
             grid-gap: 25px;
         }
         .figure{
             padding-top: 50px;
             grid-column: 3 / 7;
         }
    } 
    
// -------- ASTUCES
    .section--astuces, .section--recettes{
        padding: 50px 125px 125px 125px;
        .title--large, .title--medium{
            padding-left: 0;
        }
        .astuces__liste, .recettes__liste{
            overflow: inherit;
            white-space: inherit;
            padding: 0;
        }
        .astuces__el, .recettes__el{
            display: inline-block;
            box-shadow: 0 2px 10px rgba(0,0,0,.2);
            width: 29%;
            height: 300px;
            list-style: none;
            background-color: #fff;
            border-radius: 5px;
            vertical-align: middle;
            margin: 15px;
            position: relative;
            &:hover, &:focus{
                transform: scale(1.02);
                box-shadow: 0 2px 20px rgba(0,0,0,.3);
                transition: .3s ease-out;
                & a{
                    color: $c-red;
                    transition: .3s ease-out;
                }
            }
        }
        .astuces__el--last, .recettes__el--last{
            padding: 25px 50px;
            &:after{
                position: absolute; top: 0; right: -60px;
                background-size: 70%;
            }
            p{
                width: 55%;
            }
            &:hover{
                box-shadow: 0 2px 10px rgba(0,0,0,.2);
                transform: none;
            }
        }
        .popup-astuce__el.active, .popup-recette__el.active{
            width: calc(100% - 400px);
            height: calc(100% - 100px);
            top: 50px;
            left: 200px;
            padding: 25px 100px 50px 100px;
            & .video{
                max-width: 600px;
                margin-left: auto; margin-right: auto;
            }
        }
        .popup-close{
            top: 75px;
            right: 225px;
        }
        .bg-r, .bg-a{
            height: 78%;
            width: 100%;
            border-radius: 5px 5px 0 0;
            background-repeat: no-repeat;
            background-size: cover;
        }
    .bg-r01{
        background-position: center 65%;
    }
    .bg-r02{
        background-position: center 40%;
    }
    .bg-r03{
        background-position: center 50%;
    }
    .recettes__txt, .astuces__txt{
        background-color: #fff;
    }
    }
    .section--favoris{
        .sorry{
            width: 65%;
        }
        .poisson-big{
            display: inline-block;
            width: 25%;
            margin: 0;
            padding: 0;
            top: 50%; 
            transform: translateY(-50%);
            max-width: 150px;
        }
        .favoris__container{
            grid-column: 3 / 7;
            padding: 50px 0;
            position: relative;
        }
    }
}

