.menu{
    position: fixed;
    z-index: 100;
    box-sizing: border-box;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 55px;
    border-top-radius: 10px;
    background-color: #fff;
    border-radius: 10px 10px 0 0;
    
    .active a{
        color: $c-darkblue; 
    }
    &__liste{
        padding: 0;
        margin: 0;
        list-style: none;
        display: flex;
        justify-content: space-around;
    }
    &__el{
        
        a{
            text-decoration: none;
            color: $c-blue;
            font-weight: bold;
        }
    }
}

.avantages__liste{
    list-style: none;
    padding: 0;
    
    .avantages__el{
        padding-top: 50px;
        
        .title--medium{
            padding-top: 25px;
        }
        &:before{
            background-position: left;
            background-size: 100%;
            background-repeat: no-repeat;
            display: block;
            width: 75px;
            height: 75px;
            content: "";
        }
        &:first-child:before{
            background-image: url(../../assets/images/icon-money.svg);    
        }
        &:nth-child(2):before{
            background-image: url(../../assets/images/icon-health.svg);
        }
        &:last-child:before{
            background-image: url(../../assets/images/icon-time.svg);
        }
    }
    .finances__liste, .sante__liste{
        padding: 0;
        margin: 25px 0;
    }
}

@media (max-width: 999px){
    .menu{
        box-shadow: 0 0 18px rgba(0,0,0,.15);
        &__liste{
            height: 100%;
        }
        &__el{
            width: 25%;
            & a {
                height: 100%;
                
            } 
        }
        .active a{
            color: $c-darkblue; 
        }
        .active .menu__p:before, .active a{
            transition: .4s;
        }
        .active .menu__p--zd:before{
            background-image: url(../../assets/images/icon-home-active.svg);
        }
        .active .menu__p--ast:before{
            background-image: url(../../assets/images/icon-bulb-active.svg);
        }
        .active .menu__p--rec:before{
            background-image: url(../../assets/images/icon-cook-active.svg);
        }
        .active .menu__p--fav:before{
            background-image: url(../../assets/images/icon-coeur-active.svg);
        }
        &__p{
            display: block;
            text-align: center;
            font-size: 13px;
            line-height: 1;
            font-weight: normal;
            margin: 2px 0;
            &--zd:before{
                background-image: url(../../assets/images/icon-home.svg);
            }
            &--ast:before{
                background-image: url(../../assets/images/icon-bulb.svg);
            }
            &--rec:before{
                background-image: url(../../assets/images/icon-cook.svg);
            }
            &--fav:before{
                background-image: url(../../assets/images/icon-coeur.svg);
            }
            &:before{
                background-size: 75%;
                background-repeat: no-repeat;
                background-position: center;
                display: block;
                width: 36px;
                height: 36px;
                margin: 2px auto 2px auto;
                content: "";
            }
            &--big{
                display: none;
            }
        }
        &__icon{
            height: 34px;
            display: block;
            margin: 10px auto 0 auto;
            vertical-align: middle;
           
        }
    }
}

@include lg {
    
    .menu{
        position: absolute;
        top: 0;
        display: block;
        background-color: inherit;
        .active a{
            color: $c-darkblue; 
            & p{
                
            } 
        }
        &__liste{
            padding-top: 25px;
            flex-direction: row;
            justify-content: center;
        }
        &__el{
            margin: 0 35px; 
            
            & a{
                color: $c-blue;
                display: inline-block;
                position: relative;
                margin: 0 5px;
                &:after{
                    content: "";
                    position: absolute;
                    display: none;
                    bottom: -5px;
                    right: 0;
                    height: 14px;
                    width: 100%;
                    background-image: url(../../assets/images/feuille.svg);
                    background-repeat: no-repeat;
                    background-position: center;
                }
                &:hover{
                    &:after{
                        display: block;
                    }
                }
                &:hover, &:focus{
                    color: $c-darkblue;
                    transition: .4s ease-out;
                    transform: translateY(-2px);
                }
            }
        }
        &__p{
            margin: 10px 0;
            &--small{
                display: none;
            }
        }
    }
}
@include xl {
    .menu{
        .menu__liste{
        padding-top: 50px;
    }
        .menu__el{
        margin: 0 60px;
    }  
    }
    
}
